<template>
  <el-row type="flex" justify="center">
    <el-col :span="8">
      <div>
        <el-form :label-position="labelPosition" label-width="80px" :model="user">
          <el-form-item label="用户名">
            <el-input v-model="user.userName" />
          </el-form-item>
          <el-form-item label="密码">
            <el-input type="password" v-model="user.password" />
          </el-form-item>
          <el-form-item>
            <el-button type="primary" class="button" @click="login()" round>登录</el-button>
          </el-form-item>
        </el-form>
      </div>
    </el-col>
  </el-row>
</template>

<script>
import Qs from "qs";

export default {
  data() {
    return {
      labelPosition: "top",
      user: {
        userName: "",
        password: ""
      }
    };
  },
  methods: {
    login() {
      if (this.user.userName != null && this.user.password != null) {
        var api = "http://localhost:8888/common/login";
        this.axios
          .post(api, Qs.stringify(this.user))
          .then(response => {
            console.log(response);
            if (response.status == 200) {
              if (response.data.isLogin == false) {
                alert("登录失败，请检查用户名或密码");
              } else {
                sessionStorage.setItem("userID", response.data.userID);
                sessionStorage.setItem("staffID", response.data.staffID);
                sessionStorage.setItem("depID", response.data.depID);
                sessionStorage.setItem("userName", response.data.userName);
                sessionStorage.setItem("role", response.data.role);
                sessionStorage.setItem("loginTime",response.data.loginTime);
                sessionStorage.setItem("isLogin", response.data.isLogin);
                console.log("isLogin:" + sessionStorage.getItem("isLogin"))
                this.$message({
                  showClose: true,
                  message: "登录成功",
                  type: "success"
                });
                if(response.data.role == 0) {
                  sessionStorage.setItem("show", true);
                  this.$router.push({name: 'staffList', params: { current: 1, size: 5 }})
                }else {
                  this.$router.push({
                  name: "cPaperList",
                  params: { current: 1, size: 5 }
                });
                }
              }
            }
          })
          .catch(error => {
            console.log(error);
            this.$message({
              showClose: true,
              message: "操作失败，请联系维护人员",
              type: "error"
            });
          });
      } else {
        alert("用户名或密码不能为空");
      }
    }
  }
};
</script>

<style lang="scss" scoped>
.button {
  display: block;
  margin: 0 auto;
  width: 150px;
}
</style>